<template>

<div class="section" ref="course-search-results">
    <div class="compartment compartment--content course-offerings__compartment">
        <div class="course-offerings__results-overflow">
          <table v-if="courses" class="table table--theme-default table--borderless course-offerings__results">
            <thead>
              <tr>
                <th scope="col" @click="sort('DISPLAY_KEY')">Course</th>
                <th scope="col" @click="sort('TITLE')">Title</th>
                <th scope="col">Meets</th>
                <th scope="col" @click="sort('STATUS')">Status</th>
                <th scope="col">Pre-Req/Restrictions</th>
              </tr>
            </thead>
            <tbody>
              <tr v-if="!courses || !courses.length"><td colspan="5" style="text-align: center">No courses found</td></tr>
                <tr v-for="course in visibleCourses" :key="course.CRN">
                    <td>
                    <div class="course-offerings__results-number"><strong style="white-space: nowrap;">{{ course.DISPLAY_KEY }}</strong></div>
                    <div class="course-offerings__results-crn"><strong>CRN: </strong> {{ course.CRN }}</div>
                    </td>
                    <td>
                    <div class="course-offerings__results-title">
                        <router-link :to="{path: `/${course.TERM_CODE}/${course.CRN}`}">{{ course.TITLE }}</router-link></div>
                    <div class="course-offerings__results-faculty"><strong>Instructor: </strong>
                      <template v-if="course.INSTRUCTOR1_NAME">{{ course.INSTRUCTOR1_NAME }}</template>
                      <template v-else>TBA</template>
                      <template v-if="course.INSTRUCTOR2_NAME">, {{ course.INSTRUCTOR2_NAME }}</template>
                      <template v-if="course.INSTRUCTOR3_NAME">, {{ course.INSTRUCTOR3_NAME }}</template>
                    </div>
                    <div v-if="course.NOTES" class="course-offerings__results-notes"><strong>Notes:</strong> {{ course.NOTES }}</div>
                    <!-- This is hacked and copy/pasted in CourseOfferingsDetail, but ok for now -->
                    <div v-if="course.DELIVERY_METHOD_CODE">
                      <strong>Delivery Method: </strong>
                      <span v-if="course.DELIVERY_METHOD_CODE == null">TBD</span>
                      <span v-if="course.DELIVERY_METHOD_CODE == 'DHYB'">Hybrid</span>
                      <span v-if="course.DELIVERY_METHOD_CODE == 'DCAM'">In-Person</span>
                      <span v-if="course.DELIVERY_METHOD_CODE == 'DOLS'">Online Scheduled (Synchronous)</span>
                      <span v-if="course.DELIVERY_METHOD_CODE == 'DOLA'">Online Flexible (Asynchronous)</span>
                      <span v-if="course.DELIVERY_METHOD_CODE == 'DOLT'">Online TBD</span>
                    </div>
                    <div v-if="course.ADDITIONAL_NOTES_IND == 'Y'">
                      <strong>See additional delivery notes</strong>
                    </div>
                    </td>
                    <td class="course-offerings__results-hours">
                        <div v-if="course.MEET1_DAYS" style="white-space: nowrap;">
                            <div><strong>{{ course.MEET1_DAYS }}</strong></div>
                            <div v-if="course.MEET1_BEGIN_TIME12AM">
                              <div>{{ course.MEET1_BEGIN_TIME12AM }} - {{ course.MEET1_END_TIME12AM }}</div>
                            </div>
                            <div v-else>
                              Asynchronous
                            </div>
                        </div>
                        <div v-else>
                        TBA
                        </div>
                        <div v-if="course.MEET2_DAYS" style="white-space: nowrap;">
                            <div><strong>{{ course.MEET2_DAYS }}</strong></div>
                            <div v-if="course.MEET2_BEGIN_TIME12AM">
                              <div>{{ course.MEET2_BEGIN_TIME12AM }} - {{ course.MEET2_END_TIME12AM }}</div>
                            </div>
                            <div v-else>
                              Asynchronous
                            </div>
                        </div>
                        <div v-if="course.MEET3_DAYS" style="white-space: nowrap;">
                            <div><strong>{{ course.MEET3_DAYS }}</strong></div>
                            <div v-if="course.MEET3_BEGIN_TIME12AM">
                              <div>{{ course.MEET3_BEGIN_TIME12AM }} - {{ course.MEET3_END_TIME12AM }}</div>
                            </div>
                            <div v-else>
                              Asynchronous
                            </div>
                        </div>
                    </td>
                    <td class="course-offerings__results-status">
                    <div><strong>{{ course.STATUS }}</strong></div>
                    <template v-if="course.STATUS != 'Closed'">
                        <template v-if="course.RESERVED_SEATS">
                        <div v-for="seats in course.RESERVED_SEATS.split(',')" :key="seats.cat">
                            {{ seats }}
                        </div>
                        </template>
                        <template v-else>
                        <div>{{ course.SEATS }}</div>
                        </template>
                    </template>
                    </td>
                    <td>
                    <div>{{ course.PRE_REQS }}</div>
                    <div>{{ course.CLASS_YEAR_RESTRICTIONS }}</div>
                    <div>{{ course.MAJOR_RESTRICTIONS }}</div>
                    <div>{{ course.SPECIAL_APPROVAL }}</div>
                    <div>{{ course.CATALOG_RESTRICTIONS }}</div>
                    </td>
                </tr>

            </tbody>
          </table>

      </div>

      <Paginator :total-items="courses ? courses.length : 0" :items-per-page="itemsPerPage" :current-page="currentPage" @pagechanged="onPageChange"></Paginator>

      </div>
    </div>

</template>

<script>
import Paginator from './Paginator.vue';

export default {
  name: 'CourseOfferingsSearchResults',
  components: {
    Paginator
  },
  props: {
      courses: Array,
      itemsPerPage: Number,
      currentPage: Number,
  },
  data() {
    return {
    sortOrderAscending: {}
    }
  },
  computed: {
    visibleCourses() {
        let start = this.itemsPerPage * (this.currentPage - 1)
        return this.courses && this.courses.slice( start, start + this.itemsPerPage )
    }
  },
  watch: {
    courses: function(/* newCourses, oldCourses */) {
      //console.log('courses changed')
      window.scrollTo({ left: 0, top: this.$refs['course-search-results'].offsetTop - 75})
    }
  },
  methods: {
    onPageChange: function(page) {
        // TODO:
        // eslint-disable-next-line vue/no-mutating-props
        this.currentPage = page
        this.scrollTo('course-search-results')
    },
    sort: function(courseKey) {
      if (Object.prototype.hasOwnProperty.call(this.sortOrderAscending.hasOwnProperty, courseKey)) {
        this.sortOrderAscending[courseKey] = !this.sortOrderAscending[courseKey]
      } else {
        this.sortOrderAscending[courseKey] = true
      }

    if (this.sortOrderAscending[courseKey]) {
      // TODO:
      // eslint-disable-next-line vue/no-mutating-props
      this.courses.sort( (a, b) => a[courseKey] == b[courseKey] ? 0 : a[courseKey] < b[courseKey] ? -1 : 1)
    } else {
      // TODO:
      // eslint-disable-next-line vue/no-mutating-props
      this.courses.sort( (a, b) => a[courseKey] == b[courseKey] ? 0 : a[courseKey] > b[courseKey] ? -1 : 1)
    }
    },
    scrollTo(refName) {
      let element = this.$refs[refName];
      let top = element.offsetTop;
      window.scrollTo({ left: 0, top });
    }
  }

}
</script>

