<script>
import axios from 'axios';

export default {
  name: 'CourseOfferingsDetail',
  props: ['term_code', 'crn'],
  mounted() {
     if (process.env.NODE_ENV === 'development') {
      const plugin2 = document.createElement("script");
      plugin2.setAttribute("src", "https://www.colgate.edu/themes/custom/colgate/silc/build/js/index.js");
      plugin2.async = true;
      document.head.appendChild(plugin2);
    }
     axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/${this.term_code}/${this.crn}`)
     .then((result) => {
       this.course = result.data.length ? result.data[0] : {}
       this.$nextTick( () => {
         window.scrollTo({ left: 0, top: this.$refs['course-details'].offsetTop - 50})
       })
     })
     .catch(()/*err*/ => {
       this.error = 'Unable to retrieve course details'
     })

  },
  data () {
    return {
      error: '',
      course: {}
    }
  },
  computed: {
    _seats: function() {
      if (this.course.RESERVED_SEATS_JSON || this.course.SEATS_JSON) {
        return JSON.parse(this.course.RESERVED_SEATS_JSON || '['+this.course.SEATS_JSON+']' ) //TODO: put both in array, not just reserved
      } else {
        return ''
      }
    },
    _attributes: function() {
      return JSON.parse(this.course.ATTRIBUTES || '[]')
    },
    _liberalArtsPracticeAttributes: function() {
      return this._attributes.filter(a=>a.code.startsWith('LAP'))
    }
},
created () {
    //  axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/${this.term_code}/${this.crn}`)
