    //  })
    //  .catch(()/*err*/ => {
    //    this.error = 'Unable to retrieve course details'

    //  })
  },
  // beforeRouteUpdate (to, from, next) {
  //   // react to route changes
  //   console.log(`To: ${to}  From: ${from}`)
  //   next()
  // },
  methods: {
    getLiberalArtsPractices(attributes) {
      return attributes.filter(a=>a.code.startsWith('LAP'))
    }

  }
}
