<script>
import axios from 'axios';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

//import querystring from 'querystring';

import CourseOfferingsSearchResults from './CourseOfferingsSearchResults.vue';

export default {
  name: 'CourseOfferingsSearch',
  components: {
    CourseOfferingsSearchResults,
    Loading
  },
  activated() {

    // if (Object.keys(this.$route.query).length) {
    //   Object.assign(this.form, this.$route.query)
    //   this.searchCourses(this.$route.query)
    // }
  },

  mounted() {
    if (process.env.NODE_ENV === 'development') {
     const plugin2 = document.createElement("script");
     plugin2.setAttribute("src", "https://www.colgate.edu/themes/custom/colgate/silc/build/js/index.js");
     plugin2.async = true;
     document.head.appendChild(plugin2);
    }

    if (this.$route.query.instructorUsername) {
        this.searchCourses( { params: { instructorUsername: this.$route.query.instructorUsername} })
    }

    // if (Object.keys(this.$route.query).length) {
    //   Object.assign(this.form, this.$route.query)
    //   this.searchCourses(this.$route.query)
    // }
  },
  destroyed() {},
  data () {
    return {
      itemsPerPage: 1000,
      currentPage: 1,
      isSearching: false,
      searchError: '',
      terms: [],
      subjects: [],
      coreAreas: [{"CORE_AREA":"Challenges of Modernity"},{"CORE_AREA":"Communities"},{"CORE_AREA":"Conversations"},{"CORE_AREA":"Global Engagements"},{"CORE_AREA":"Legacies of Ancient World"},{"CORE_AREA":"Sciences"}],
      inquiryAreas: [{"INQUIRY_AREA":"Human Thought and Expression"},{"INQUIRY_AREA":"Natural Sciences & Mathematics"},{"INQUIRY_AREA":"Social Relations,Inst.& Agents"}],
      liberalArtsPracticeAreas: [{"ATTR_CODE":"LAPA", "ATTR_DESC": "Artistic Prac & Interpretation"},{"ATTR_CODE":"LAPC","ATTR_DESC":"Confront Collective Challenges"},{"ATTR_CODE":"LAPL","ATTR_DESC":"Language Study"},{"ATTR_CODE":"LAPQ","ATTR_DESC":"Quant & Algorithmic Reasoning"},{"ATTR_CODE":"LAPW","ATTR_DESC":"The Process of Writing"}],
      deliveryMethods: [{"METHOD_CODE":"DCAM","METHOD_DESC":"In-Person"},{"METHOD_CODE":"DHYB","METHOD_DESC":"Hybrid"},{"METHOD_CODE":"DOLA","METHOD_DESC":"Online Flexible (Asynchronous)"},{"METHOD_CODE":"DOLS","METHOD_DESC":"Online Scheduled (Synchronous)"}],
      courses: null,
      scrollToResults: true,

      form: {
          keyword: '',
          termCode: '',
          program: [],
          coreArea: '',
          inquiryArea: '',
          liberalArtsPracticeArea: '',
          deliveryMethod: [],
          meetDays: [],
          meetTimeMorning: '',
          meetTimeAfternoon: '',
          meetTimeEvening: '',
          levels: [],
          creditHrs: [],
          openCoursesOnly: ''
      }
    }
  },
  created () {
     if (Object.keys(this.$route.query).length) {

      // TODO:  THIS WILL FAIL if a multi-valued option is set to a single value, because the underlying array will be replaced with single
      // Don't forget to check checkboxes also (eg openCoursesOnly)
      Object.assign(this.form, this.$route.query)

//       for (const p in a) {
// ... console.log(`${p} = ${typeof a[p]} = ${a[p] instanceof Array}`)
// ... }


      this.searchCourses(this.$route.query)
    } else {
      //console.log('no query')
    }

     // retrieve subject areas
     axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/search/program-areas`)
     .then((result) => {
       this.subjects = result.data
     })
     .catch(()/*err*/ => {
       //console.error(err)
     })

    // retrieve core areas
    // August 2023 - switched to hard-coded lists
    // axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/search/core-areas`)
    //  .then((result) => {
    //    this.coreAreas = result.data
    //  })
    //  .catch(()/*err*/ => {
    //    //console.error(err)
    //  })

    // // retrieve inquiry areas
    // axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/search/inquiry-areas`)
    //  .then((result) => {
    //    this.inquiryAreas = result.data
    //  })
    //  .catch(()/*err*/ => {
    //    //console.error(err)
    //  })

     // retrieve terms
      axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/search/terms`)
      .then((result) => {
        this.terms = result.data
        this.form.termCode = this.terms.length ? this.terms[0].TERM_CODE : ''
      })
      .catch(() /*err*/ => {
          //console.error(err)
      })
  },
  // see https://stackoverflow.com/questions/49958366/state-history-with-browser-back-button-vue-js
  methods: {
    searchCourses: async function ( { params = this.form}) {

      // No, we are not going to save the search into the URL bar because it is way too confusing
      // if you go to a search and have no idea what options are already chosen
      // this.$router.push({ query: this.form })

      this.isSearching = true
      try {
       const { data } = await axios.get(`${process.env.VUE_APP_BASE_API_URL}/v1/courses/search`, { params })

        this.isSearching = false
        this.courses = data
        this.currentPage = 1

        if (this.courses.length) {
            // note that this.$refs.results.$el doesnt work if using vue runtime build
            // ADD THIS TO RESULTS COMPONENT AS REACTIVE
            window.scrollTo({ left: 0, top: this.$refs.results.$el.offsetTop - 75})
        }
      } catch (err) {
          this.isSearching = false
          this.searchError = 'Search error'
      }
    },
    scrollTo(refName) {
      let element = this.$refs[refName];
      let top = element.offsetTop
      window.scrollTo({ left: 0, top });
    }
  },
   watch: {
            // '$route.query'() {
            // Object.assign(this.form, this.$route.query)
            // this.searchCourses(this.$route.query)
            // }
        },
}
</script>

