<template>
  <nav class="pagination pagination--block" aria-label="View pages of search results">

  <ul class="pagination__list" v-if="totalItems > itemsPerPage">
    <li class="pagination__list-item pagination__list-item--prev">
      <a :href="!isInFirstPage" aria-label="Go to previous page" title="Go to previous page" rel="prev" @click.prevent v-on="!isInFirstPage ? { click: onClickPreviousPage} : {}"></a>
    </li>
    <li class="pagination__list-item" v-for="page in pages" :key="page.name" :class="{ 'is-active': isPageActive(page.name) }">
      <a v-if="page.isDisabled" :disabled="page.isDisabled">{{ page.name }}</a>
      <a v-else :aria-label="`Go to page ${page.name}`" title="`Go to page ${page.name}`" @click.prevent="onClickPage(page.name)">
        <span role="presentation">{{ page.name }}</span>
      </a>
    </li>
    <li class="pagination__list-item">
      <span role="presentation">&hellip;</span>
    </li>
    <li class="pagination__list-item pagination__list-item--next">
      <a :href="!isInLastPage" rel="next" aria-label="Go to next page" title="Go to next page" @click.prevent v-on="!isInLastPage ? { click: onClickNextPage} : {}"></a>
    </li>
  </ul>
</nav>
</template>

<script>
export default {
  name: 'Paginator',
  
  props: {
    maxVisibleDelta: {
      type: Number,
      required: false,
      default: 2
    },
    totalItems: {
      type: Number,
      required: true
    },
    itemsPerPage: {
      type: Number,
      required: true,
      default: 10
    },
    currentPage: {
      type: Number,
      required: false,
      default: 1
    },
  },computed: {
    totalPages() {
        return Math.ceil(this.totalItems/this.itemsPerPage)
    },
    pages() {
      const delta = this.maxVisibleDelta

    const range = [];
    for (let i = Math.max(2, (this.currentPage - delta)); i <= Math.min((this.totalPages - 1), (this.currentPage + delta)); i += 1) {
        range.push(
          {
          name: i,
          isDisabled: i === this.currentPage 
        }
        );
    }

    if ((this.currentPage - delta) > 2) {
        range.unshift({
          name: '...',
          isDisabled: true 
        });
    }
    if ((this.currentPage + delta) < (this.totalPages - 1)) {
        range.push({
          name: '...',
          isDisabled: true 
        });
    }

    range.unshift({
          name: 1,
          isDisabled: 1 === this.currentPage 
        })
    ;
    if (this.totalPages !== 1) range.push(
      {
          name: this.totalPages,
          isDisabled: this.totalPages === this.currentPage 
        });

    return range;
    },
    isInFirstPage() {
      return this.currentPage === 1;
    },
    isInLastPage() {
      return this.currentPage === this.totalPages;
    },
  },
  methods: {
    onClickFirstPage() {
      this.$emit('pagechanged', 1);
    },
    onClickPreviousPage() {
      this.$emit('pagechanged', this.currentPage - 1);
    },
    onClickPage(page) {
      this.$emit('pagechanged', page);
    },
    onClickNextPage() {
      this.$emit('pagechanged', this.currentPage + 1);
    },
    onClickLastPage() {
      this.$emit('pagechanged', this.totalPages);    
    },
    isPageActive(page) {
      return this.currentPage === page;
    },
  }
  
  
}
</script>

