import Vue from 'vue'
import Router from 'vue-router'
import CourseOfferings from './views/CourseOfferings.vue'
import CourseOfferingsDetail from './views/CourseOfferingsDetail.vue'

Vue.use(Router)

// consider finer control, e.g. https://github.com/vuejs/vue-router/blob/dev/examples/scroll-behavior/app.js
/*
const scrollBehavior = function (to, from, savedPosition) {
  if (savedPosition) {
    return savedPosition
  } else {
    return { x: 0, y: 0 }
  }
}
*/
const scrollBehavior = function (to, from, savedPosition) {
  if (savedPosition) {
    // savedPosition is only available for popstate navigations.
    return savedPosition
  } else {
    const position = {}

    if (to.hash) {
      position.selector = to.hash

      if (document.querySelector(to.hash)) {
        return position
      }

      // if the returned position is falsy or an empty object,
      // will retain current scroll position.
      return false
    }

    return new Promise(resolve => {
      // check if any matched route config has meta that requires scrolling to top
      if (to.matched.some(m => m.meta.scrollToTop)) {
        // coords will be used if no selector is provided,
        // or if the selector didn't match any element.
        position.x = 0
        position.y = 0
      }

      // wait for the out transition to complete (if necessary)
      this.app.$root.$once('triggerScroll', () => {
        // if the resolved position is falsy or an empty object,
        // will retain current scroll position.
        resolve(position)
      })
    })
  }
}

export default new Router({
 // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'courseofferings',
      component: CourseOfferings
//      redirect: { name: 'courseofferings' }
    },
    // {
    //   path: '/courseofferings',
    //   name: 'courseofferings',
    //   component: CourseOfferings
    // },
    {
     // path: '/courseofferings/:term_code/:crn',
     path: '/:term_code/:crn',
      name: 'courseofferings-section',
      component: CourseOfferingsDetail,
      props: true
    }
  ],
  scrollBehavior
})
